// src/api/news.js
const API_BASE = process.env.REACT_APP_NEWS_API_BASE;

/**
 * HTTP   
 */

async function apiRequest(endpoint, options = {}) {
  try {
    const url = `${API_BASE}${endpoint}`;
    console.log('API :', url); // 
    
    const response = await fetch(url, {
      headers: {
        'Content-Type': 'application/json',
        ...options.headers,
      },
      ...options,
    });

    if (!response.ok) {
      const errorText = await response.text();
      console.error('API  :', response.status, errorText);
      throw new Error(`HTTP ${response.status}: ${response.statusText}`);
    }

    // 204 No Content 
    if (response.status === 204) {
      console.log('API : No Content (204)');
      return null;
    }

    // JSON     /
    const text = await response.text();
    if (!text) return null;
    const data = JSON.parse(text);
    console.log('API  :', data); // 
    return data;
  } catch (error) {
    console.error('API  :', error);
    throw error;
  }
}

/**
 *    
 * @param {number} skip -    ()
 * @param {number} limit -    ( 100)
 */
export const getTodayNews = async (skip = 0, limit = 20) => {
  return await apiRequest(`/api/articles/today?skip=${skip}&limit=${limit}`);
};

/**
 *    
 * @param {string} category -  (, ,  ,   )
 * @param {number} skip -   
 * @param {number} limit -   
 */
export const getNewsByCategory = async (category, skip = 0, limit = 20) => {
  const encodedCategory = encodeURIComponent(category);
  return await apiRequest(`/api/articles/category/${encodedCategory}?skip=${skip}&limit=${limit}`);
};

/**
 *     
 * @param {number} articleId -  ID
 */
export const getArticleDetail = async (articleId) => {
  return await apiRequest(`/api/articles/${articleId}`);
};

/**
 * / 
 * @param {string} q - 
 * @param {number} skip - 
 * @param {number} limit - ( 100)
 */
export const searchArticles = async (q, skip = 0, limit = 20) => {
  const encoded = encodeURIComponent(q);
  return await apiRequest(`/api/articles/search?q=${encoded}&skip=${skip}&limit=${limit}`);
};

/**
 *   (     )
 */
export const CATEGORY_MAPPING = {
  ' ': 'today', //   (getTodayNews )
  '': '',
  '': '', 
  ' ': ' ',
  ' ': ' '
};

/**
 *     (   API )
 * @param {string} category - 
 * @param {number} skip -   
 * @param {number} limit -   
 */
export const getNewsData = async (category = ' ', skip = 0, limit = 20) => {
  try {
    if (category === ' ') {
      return await getTodayNews(skip, limit);
    } else {
      const mappedCategory = CATEGORY_MAPPING[category] || category;
      return await getNewsByCategory(mappedCategory, skip, limit);
    }
  } catch (error) {
    console.error(`${category}   :`, error);
    throw error;
  }
};

// ---------------- Admin () ----------------
const ADMIN_KEY = process.env.REACT_APP_ADMIN_KEY;

export const adminSoftDelete = async (articleId, { reason = '', lockHours = 24 } = {}) => {
  const params = new URLSearchParams();
  if (reason) params.append('reason', reason);
  if (lockHours != null) params.append('lock_hours', String(lockHours));
  return await apiRequest(`/api/articles/admin/${articleId}?${params.toString()}`, {
    method: 'DELETE',
    headers: ADMIN_KEY ? { 'X-ADMIN-KEY': ADMIN_KEY } : {},
  });
};

export const adminRestore = async (articleId) => {
  return await apiRequest(`/api/articles/admin/${articleId}/restore`, {
    method: 'POST',
    headers: ADMIN_KEY ? { 'X-ADMIN-KEY': ADMIN_KEY } : {},
  });
};

export const adminPurge = async (articleId) => {
  return await apiRequest(`/api/articles/admin/${articleId}/purge`, {
    method: 'DELETE',
    headers: ADMIN_KEY ? { 'X-ADMIN-KEY': ADMIN_KEY } : {},
  });
};